// app/attendant-shifts/page.tsx
"use client";

import { useEffect, useState } from "react";
import { useAuth } from "@/context/auth-context";
import { Button, Card } from "@/components/ui"; // example UI components

interface Shift {
  id: number;
  shiftType: string;
  scheduledDate: string;
  scheduledStartTime: string;
  scheduledEndTime: string;
  status: string;
  assignedPump?: string;
  notes?: string;
}

export default function AttendantShiftsPage() {
  const { user, token } = useAuth();
  const [shifts, setShifts] = useState<Shift[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!token) return; // prevent fetching if not logged in

    const fetchShifts = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/shifts/my-shifts?upcoming=true", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (!res.ok) throw new Error("Failed to fetch shifts");

        const data = await res.json();
        setShifts(data);
      } catch (err) {
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchShifts();
  }, [token]);

  const handleStartShift = async (shiftId: number) => {
    try {
      const res = await fetch(`http://localhost:5000/api/shifts/start/${shiftId}`, {
        method: "PUT",
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!res.ok) throw new Error("Failed to start shift");
      const updated = await res.json();
      setShifts((prev) =>
        prev.map((s) => (s.id === shiftId ? { ...s, status: "IN_PROGRESS" } : s))
      );
    } catch (err) {
      console.error(err);
    }
  };

  const handleEndShift = async (shiftId: number) => {
    try {
      const res = await fetch(`http://localhost:5000/api/shifts/end/${shiftId}`, {
        method: "PUT",
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!res.ok) throw new Error("Failed to end shift");
      const updated = await res.json();
      setShifts((prev) =>
        prev.map((s) => (s.id === shiftId ? { ...s, status: "COMPLETED" } : s))
      );
    } catch (err) {
      console.error(err);
    }
  };

  if (loading) return <p>Loading shifts...</p>;
  if (!shifts.length) return <p>No shifts found</p>;

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4 p-4">
      {shifts.map((shift) => (
        <Card key={shift.id} className="p-4">
          <h3 className="font-bold">{shift.shiftType} Shift</h3>
          <p>Date: {shift.scheduledDate}</p>
          <p>
            Time: {shift.scheduledStartTime} - {shift.scheduledEndTime}
          </p>
          <p>Status: {shift.status}</p>
          <p>Pump: {shift.assignedPump || "N/A"}</p>

          {shift.status === "SCHEDULED" && (
            <Button onClick={() => handleStartShift(shift.id)}>Start Shift</Button>
          )}
          {shift.status === "IN_PROGRESS" && (
            <Button onClick={() => handleEndShift(shift.id)}>End Shift</Button>
          )}
        </Card>
      ))}
    </div>
  );
}
